<template>
	<div class="black-bg" v-if="modalOpen == true">
		<div class="white-bg">
			<img :src="goodsInfoData[isClick].image" alt="" class="room_img">
			<h4>{{ goodsInfoData[isClick].title }}</h4>
			<p>{{ goodsInfoData[isClick].content}}</p>
			<input type="text" :value="month" @input="month = $event.target.value"> 
			<!-- @input="month = $event.target.value : 사용자가 입력한값-->
			<p>{{month}} 개월 선택함 : {{ goodsInfoData[isClick].price * month}} 만원</p>
			<button type="button" @click="$emit('closePop')">닫기</button>
		</div>
	</div>
</template>

<!-- 
	watcher
	: 데이터가 제대로 들어왔는지, 이상한 값이 들어왔는지, data 감시하는 함수
	사용자의 input을 받는 페이지다 그러면 필수로 필요함.
	사용자가 input에 이상하게 입력할 수 있으므로

	watch : {} 라는 항목을 신설해서 거기다가 작성해주시면 되며
	어떤 데이터를 계속 감시하는 역할을 하는 코드를 적을 수 있습니다. 
	감시한다는게 어려운건 아니고 그냥 특정 데이터가 변경될 때마다 
	실행되는 코드를 여기 적을 수 있습니다.
-->

<script>

export default {
	name : 'discountModal',
	data() {
		return {
			month : 1,
		}
	},
	watch : {
		month(a){
			//감시할데이터 만들기
			// a 는 month에 입력한 데이터가 꽂힐것임 (변경후 데이터)
			// month(a,b) 하나 더 올수가 있는데 b 는 변경전 데이터
			//사용자가 month를 글자로 입력하면 경고문 띄워주셈!
			if (a >= 13) {
				alert("1개월 부터 12개월까지 가능합니다!");
				this.month = 1;
				
			}
			if(/[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/.test(this.month)){
				alert('숫자만 입력가능합니다.');
				this.month = 1;
				
			}

			/*
			if (isNaN( this.month)){
				alert("숫자만 입력 가능합니다.");
				this.month = 1;
			}
			*/

		}
	},
	props : {
		//데이터를 받아왔다 기록하기
		goodsInfoData : Array,
		isClick : Number,
		modalOpen : Boolean,
	}
}
</script>

<style>

.white-bg {max-width:500px; margin:0 auto;}
.white-bg img {width:100%;}

input {text-align:center;}

</style>
